import { Router } from 'express';
import { container } from '../config/inversify.config';
import { TYPES } from '../types';
import { SystemController } from '../controllers/system.controller';

const router = Router();
const controller = container.get<SystemController>(TYPES.SystemController);

router.get('/metrics', controller.getSystemMetrics.bind(controller));
router.get('/cpu', controller.getCPUUsage.bind(controller));
router.get('/memory', controller.getMemoryUsage.bind(controller));
router.get('/disk', controller.getDiskUsage.bind(controller));
router.get('/network', controller.getNetworkStats.bind(controller));

export default router;
